.navbar {
    position: fixed;
    bottom: 0;
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
}

.navbar-icon {
    -ms-flex: 1 1;
        flex: 1 1;
    width: 1.5em;
    height: 1.5em;
    padding: 0.4em;
    background-color: white;
    opacity: 0.7;
}

.navbar-icon > img {
    width: 1.5em;
    opacity: 0.7;
    display: block;
    margin: 0 auto;
}

.cur-page {
    opacity: 1;
}

.cur-page > img {
    opacity: 1;
}



